<template>
    <i 
        :style="css_background()"
        class="icon" >
    </i>
</template>

<script>
    export default {
        props: ['pic'],
        methods: {
            css_background () {
                return {
                    'background-image': `url('${this.pic}')`
                }
            }
        }
    }
</script>

<style scoped>
    .icon{
        display: inline-block;
        width: .3rem;
        height: .3rem;
        background-size: 100%;
        background-position: center;
        background-repeat: no-repeat;
    }
</style>


