<template>
    <div class="home-header">
        <!-- left -->
        <Icon 
            class="left" 
            :pic="require('@/assets/images/back.png')" />
        <!-- center -->
        <div 
            class="center" 
            :style="css_center()" >
            输入城市/景点/游玩主题</div>
        <!-- right -->
        <router-link to="/city">
            <div class="right">{{city}}</div>
        </router-link>
    </div>
</template>

<script>
    import store from '@/vuex/index.js'
    import {mapState} from 'vuex'
    import Icon from '@/components/base/icon'

    export default {
        store,
        components: { Icon },
        data: ()=>({
            pic_search: require('../../../../src/assets/images/search.png'),

        }),
        computed: {
            ...mapState(['city'])
        },
        methods: {
            css_center () {
                return {
                    'background-image': `url('${this.pic_search}')`
                }
            }
        }
    }
</script>

<style lang="stylus" scoped>
    @import '~@/assets/style/varibles.styl'

    .home-header
        height $city_H
        background $blue
        display flex
        align-items center
        color #ffffff
        .left
            height .86rem
            width .86rem
            background-size: 60%;
        .center
            height .59rem
            flex auto
            padding 0 .12rem
            background #ffffff
            border-radius .12rem
            line-height .59rem
            color #aea7a7
            padding-left .6rem
            background-position 3% center
            background-size auto 50%
            background-repeat no-repeat
        .right
            color #ffffff
            height .86rem
            min-width .5rem
            background $backgroundColor 
            display flex
            align-items center
            padding-left .21rem
            padding-right .68rem
            background-image url('../../../assets/images/down.png')
            background-size auto 50%
            background-repeat no-repeat
            background-position 88% center
                
</style>



