          "p": "这次运气很好 遇到开业特惠 这个价位真心划算 4月30号去的 中午的样子到的 换票很方便 人也不多 很快就办完入园了 交了100押金 手环比较方便 里面有50元可以应急 里面吃的东西是要贵一点 不过也还是可以接受的价位 耍水记得带上手机防水袋 泳衣 浴巾那些 如果有条件最好带吹风 免得排队等 里面有拖鞋  救生衣 对于不会游泳的我来说觉得这个不错 在造浪池就不怕了  唯一一点觉得失望的是 本来以为都是温水 结果游玩区是冷水 耍起还是比较冷  里面的所有项目都是可以重复无限次的耍 每次进出游玩区大门都是要刷手环的 所以手环千万不能掉 对于想找点刺激又不是很吓人的我来说 合家欢和大喇叭都尝试了下 可以接受 还是比较好耍 不过最喜欢的是竞赛滑道 这个我耍了3次 用滑毯一点不吓人 又有一点点小刺激 耍累了可以去泡泡温泉 还是挺舒服的 一直耍到下午4点过才出来 总的来说还是不错的 值得一去 耍一整天没问题 发图给大家参考参考",
