<template>
    <div class="header">
        <router-link to="/">
            <i class="iconfont icon-fanhui"></i>
        </router-link>
        选择城市
    </div>
</template>

<script>
    export default {
        name: 'cityHeader'
    }
</script>

<style lang="stylus" scoped>
    @import '~@/assets/style/varibles.styl'

    .header
        height $header_H
        background $blue
        line-height $header_H
        text-align center
        position relative
        width 100%
        color #ffffff
        i 
            color #ffffff
            position absolute
            left 0
            top 0
            width $header_H
            height $header_H
</style>



