<template>
    <div class="recommend" v-if="list">
        <h1> 猜你喜欢</h1>
        <m-card v-for="(item, index) in list" :key="index" :itemData="item"></m-card>
        <h1>查看更多</h1>
    </div>
</template>

<script>
    import mCard from './Recommend_card.vue'
    export default {
        components: {mCard},
        props: {
            list: {
                type: Array,
                default: null
            }
        }
    }
</script>

<style lang="stylus" scoped>
    .recommend
        margin-top .21rem
        h1
            height .7rem
            line-height .7rem
            background #fff
            &:first-child
                padding-left .8rem
                background-image url('../../../assets/images/love.png')
                background-size auto 60%
                background-repeat no-repeat
                background-position 3.5% center
            &:last-child
                text-align center 

</style>



