<template>
    <div class="detail-header">
        <!-- 横条状态 -->
        <div 
            class="header" 
            :style="css_header" 
            v-show="!show_btn">
            <i class="back" @click="turnTo"/>
            <span>景点详情</span>
        </div>
        <!-- 圆形状态 -->
        <div 
            class="btn" 
            v-show="show_btn" 
            @click="turnTo" />
    </div>
</template>

<script>
    export default {
        data () {
            return {
                show_btn: true,
                css_header: {
                    opacity: 0
                }
            }
        },
        methods: {
            scroll () {
                let top = document.body.scrollTop||document.documentElement.scrollTop
                let opacity = top/140
                opacity = opacity>1?1:opacity
                // console.log(top, opacity)
                this.css_header = {opacity}
                if (top>60) {
                    this.show_btn = false
                } else {
                    this.show_btn = true
                }
            },
            turnTo () {
                this.$router.push('/')
            }
        },
        mounted () {
            window.addEventListener('scroll', this.scroll)
        },
        activated () {
            window.addEventListener('scroll', this.scroll)
        },
        deactivated () {
            window.removeEventListener('scroll', this.scroll)
        }
    }
</script>

<style lang="stylus" scoped>
    @import '~@/assets/style/varibles.styl'

    .detail-header
        position fixed
        top 0
        width 100%
        max-width 750px
        .header
            color #ffffff
            height $header_H
            line-height $header_H
            text-align center
            background $blue
            position relative
            .back
                display inline-block
                position absolute
                left 0
                height $header_H
                width $header_H
                background-image url('../../../assets/images/back.png')
                background-size 60%
                background-repeat no-repeat
                background-position center

        .btn
            border-radius .4rem
            width .8rem
            height .8rem
            background #000
            position absolute
            color #fff
            top .3rem
            left .3rem
            opacity .6
            background-image url('../../../assets/images/back.png')
            background-size 60%
            background-repeat no-repeat
            background-position center
</style>


